'use client'

import { useForm } from 'react-hook-form'
import { zodResolver } from '@hookform/resolvers/zod'
import * as z from 'zod'
import { Button } from '@/components/ui/button'
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from '@/components/ui/form'
import { Input } from '@/components/ui/input'
import { Textarea } from '@/components/ui/textarea'
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select'
import { useState } from 'react'
import { ImageUpload } from '@/components/ui/image-upload'
import { Checkbox } from '@/components/ui/checkbox'

// Base schema for all facility types
const baseSchema = z.object({
  name: z.string().min(1, 'Name is required'),
  description: z.string().min(1, 'Description is required'),
})

const RENTAL_PLANS = ['Annual', 'Monthly', 'Weekly', 'One Day (24 Hours)'] as const
type RentalPlan = typeof RENTAL_PLANS[number]

// Type-specific schemas
const facilitySchemas = {
  'individual-cabin': baseSchema.extend({
    totalCabins: z.number().min(1, 'Must have at least 1 cabin'),
    availableCabins: z.number().min(0, 'Cannot be negative'),
    videoLink: z.string().url('Please enter a valid URL').optional(),
    rentalPlans: z.array(z.enum(RENTAL_PLANS)).min(1, 'Select at least one rental plan'),
    rentPerYear: z.number().min(0, 'Price cannot be negative').optional(),
    rentPerMonth: z.number().min(0, 'Price cannot be negative').optional(),
    rentPerWeek: z.number().min(0, 'Price cannot be negative').optional(),
    dayPassRent: z.number().min(0, 'Price cannot be negative').optional(),
  }),
  'coworking-spaces': baseSchema.extend({
    totalSeats: z.number().min(1, 'Must have at least 1 seat'),
    availableSeats: z.number().min(0, 'Cannot be negative'),
    pricePerDay: z.number().min(0, 'Price cannot be negative'),
  }),
  'meeting-rooms': baseSchema.extend({
    capacity: z.number().min(1, 'Must have at least 1 person capacity'),
    pricePerHour: z.number().min(0, 'Price cannot be negative'),
    amenities: z.array(z.string()),
  }),
  // Add schemas for other facility types...
}

type FacilityFormProps = {
  type: string
  onSubmit: (data: any) => void
}

export function FacilityForm({ type, onSubmit }: FacilityFormProps) {
  const [images, setImages] = useState<string[]>([])
  const schema = facilitySchemas[type as keyof typeof facilitySchemas] || baseSchema

  const form = useForm<z.infer<typeof schema>>({
    resolver: zodResolver(schema),
    defaultValues: {
      name: '',
      description: '',
      rentalPlans: [],
    },
  })

  // Get the current value of rental plans
  const selectedRentalPlans = form.watch('rentalPlans') || []

  const handleSubmit = (values: z.infer<typeof schema>) => {
    // Validate that at least one image is uploaded
    if (images.length === 0) {
      form.setError('root', {
        type: 'manual',
        message: 'Please upload at least one image'
      })
      return
    }
    onSubmit({ ...values, images })
  }

  const renderTypeSpecificFields = () => {
    switch (type) {
      case 'individual-cabin':
        return (
          <>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <FormField
                control={form.control}
                name="name"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Facility Name *</FormLabel>
                    <FormControl>
                      <Input {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <FormField
                control={form.control}
                name="description"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Description *</FormLabel>
                    <FormControl>
                      <Textarea {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <FormField
                control={form.control}
                name="totalCabins"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Total No. of individual cabins available *</FormLabel>
                    <FormControl>
                      <Input type="number" {...field} onChange={e => field.onChange(Number(e.target.value))} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <FormField
                control={form.control}
                name="availableCabins"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>No. of Cabins currently available for occupancy *</FormLabel>
                    <FormControl>
                      <Input type="number" {...field} onChange={e => field.onChange(Number(e.target.value))} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>

            <div className="space-y-4">
              <FormLabel>Kindly upload the photos of the Individual Cabins *</FormLabel>
              <FormControl>
                <ImageUpload
                  value={images}
                  onChange={setImages}
                  onRemove={(url) => setImages(images.filter((val) => val !== url))}
                />
              </FormControl>
              {form.formState.errors.root && (
                <p className="text-sm text-destructive">{form.formState.errors.root.message}</p>
              )}
            </div>

            <FormField
              control={form.control}
              name="videoLink"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>If available, kindly attach Video Link for your Facility</FormLabel>
                  <FormControl>
                    <Input type="url" placeholder="Google Drive / Youtube link" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />

            <FormField
              control={form.control}
              name="rentalPlans"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Rental Subscription Plan(s) Available *</FormLabel>
                  <FormControl>
                    <div className="space-y-2">
                      {RENTAL_PLANS.map((plan) => (
                        <div key={plan} className="flex items-center space-x-2">
                          <Checkbox
                            checked={field.value?.includes(plan)}
                            onCheckedChange={(checked) => {
                              const updatedPlans = checked
                                ? [...(field.value || []), plan]
                                : field.value?.filter((p) => p !== plan) || []
                              field.onChange(updatedPlans)
                              
                              // Clear the corresponding rent value if plan is unchecked
                              if (!checked) {
                                switch (plan) {
                                  case 'Annual':
                                    form.setValue('rentPerYear', undefined)
                                    break
                                  case 'Monthly':
                                    form.setValue('rentPerMonth', undefined)
                                    break
                                  case 'Weekly':
                                    form.setValue('rentPerWeek', undefined)
                                    break
                                  case 'One Day (24 Hours)':
                                    form.setValue('dayPassRent', undefined)
                                    break
                                }
                              }
                            }}
                          />
                          <label className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70">
                            {plan}
                          </label>
                        </div>
                      ))}
                    </div>
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              {selectedRentalPlans.includes('Annual') && (
                <FormField
                  control={form.control}
                  name="rentPerYear"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Rent per Year (in Rupees) *</FormLabel>
                      <FormControl>
                        <Input type="number" {...field} onChange={e => field.onChange(Number(e.target.value))} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              )}
              {selectedRentalPlans.includes('Monthly') && (
                <FormField
                  control={form.control}
                  name="rentPerMonth"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Rent per Month (in Rupees) *</FormLabel>
                      <FormControl>
                        <Input type="number" {...field} onChange={e => field.onChange(Number(e.target.value))} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              )}
              {selectedRentalPlans.includes('Weekly') && (
                <FormField
                  control={form.control}
                  name="rentPerWeek"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Rent per Week (in Rupees) *</FormLabel>
                      <FormControl>
                        <Input type="number" {...field} onChange={e => field.onChange(Number(e.target.value))} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              )}
              {selectedRentalPlans.includes('One Day (24 Hours)') && (
                <FormField
                  control={form.control}
                  name="dayPassRent"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Day Pass Rent (1 Day) (in Rupees) *</FormLabel>
                      <FormControl>
                        <Input type="number" {...field} onChange={e => field.onChange(Number(e.target.value))} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              )}
            </div>
          </>
        )
      // Add cases for other facility types...
      default:
        return null
    }
  }

  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(handleSubmit)} className="space-y-6">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          {renderTypeSpecificFields()}
        </div>

        <div className="flex justify-end">
          <Button type="submit" className="w-full md:w-auto">
            Add Facility
          </Button>
        </div>
      </form>
    </Form>
  )
} 